@import "./palette";
@mixin button($color: $palette-primary-main, $contrast-text: $palette-primary-contrast-text) {
    background-color: $color;
    color: $contrast-text;
    border: none;
    padding: 12px 24px;
    border-radius: 10px;
    cursor: pointer;
    font-weight: 900;
    transition: all ease 0.2s;
    text-decoration:none;
  
    &:hover {
      background-color: darken($color, 10%);
    }
  
    &:active {
      box-shadow: inset 0 2px 0 0 rgba(0, 0, 0, 0.1),
       inset 0 2px 2px 2px rgba(0, 0, 0, 0.2),
       inset 0 0 2px 2px rgba(0, 0, 0, 0.1);
    }
  }

  @mixin select {
      padding: 8px 12px;
      border-radius: 10px;
      border: 1px solid black;
      width: 100%;
      max-width: 200px;
      box-shadow: 0px 1em 2em -1.5em rgba(0, 0, 0, 0.5);
  }